.paket {
  padding-top: 0;
  &__container {
  }

  &__title {
    margin: 0;
    margin-bottom: 80px;

    color: #141024;
    font-family: Jost;
    font-size: 50px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 80px */

    @include mobile {
      margin-bottom: 40px;

      color: #141024;
      text-align: center;
      font-size: 30px;
      font-style: normal;
      font-weight: 600;
      line-height: 140%; /* 42px */
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include high-tablet {
      gap: 20px;
    }

    @include small-tablet {
      grid-template-columns: repeat(8, 1fr);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 4 span;
    border: 1px solid rgba(20, 16, 36, 0.2);
    border-radius: 20px;
    padding: 40px;

    &-active {
      border: 1px solid #141024;
      background-color: #141024;
      transform: scale(1.1);

      .paket__name {
        color: #fff;
      }

      .paket__price {
        color: #fff;
      }

      .paket__descr {
        color: #fff;
      }

      .paket__btn {
        margin-top: 50px;
      }

      @include high-desktop {
        transform: scale(1);
      }
    }

    @include small-tablet {
      grid-column: 8 span;
    }
  }

  &__name {
    display: flex;
    align-items: center;
    margin: 0;
    margin-bottom: 60px;

    font-weight: 600;
    font-size: 30px;
    line-height: 160%;
    text-align: center;
    color: #141024;

    @include mobile {
      margin-bottom: 40px;

      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      text-align: center;
    }

    &::before {
      content: "";
      display: block;
      margin-right: 10px;
      width: 20px;
      height: 3px;
      background-color: currentColor;
    }

    &::after {
      content: "";
      display: block;
      margin-left: 10px;
      width: 20px;
      height: 3px;
      background-color: currentColor;
    }
  }

  &__price {
    margin: 0;
    margin-bottom: 20px;
    color: #141024;
    text-align: center;
    font-family: Jost;
    font-size: 50px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 80px */

    @include high-desktop {
      font-size: 45px;
    }

    @include high-tablet {
      font-size: 33px;
    }

    @include mobile {
      font-weight: 600;
      font-size: 46px;
      line-height: 160%;
      text-align: center;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: auto;

    color: #141024;
    text-align: center;
    font-family: Jost;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 28.8px */

    @include mobile {
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
    }
  }

  &__btn {
    margin-top: 60px;
    min-width: 100%;

    border-radius: 5px;
    color: #fff;
    text-align: center;
    font-family: Jost;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 28.8px */
    background: #d52027;

    @include high-tablet {
      padding: 10px 20px;
      font-size: 16px;
    }

    @include mobile {
      margin-top: 40px;

      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
    }
  }
}
