.footer {
  padding: 40px 0 20px;
  background-color: #141024;

  &__container {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: flex-start;

    @media (max-width: 700px) {
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;
      gap: 20px;
    }
  }

  &__logo {
    display: inline-block;
    order: 1;

    @include mobile {
      max-width: 140px;
    }
  }

  &__descr {
    margin: 0;

    color: rgba(255, 255, 255, 0.7);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;

    order: 3;

    @include mobile {
      font-weight: 400;
      font-size: 14px;
      line-height: 160%;
      text-align: center;
      color: rgba(255, 255, 255, 0.7);
    }
  }

  &__phone {
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    font-size: 30px;
    font-weight: 600;

    order: 2;

    @media (max-width: 700px) {
      position: relative;
      margin-bottom: 20px;
    }

    @include mobile {
      font-weight: 600;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
      color: #fff;
    }
  }
}
