.trainers {
  &__title {
    color: #141024;
    font-size: 50px;
    font-weight: 600;
    line-height: 160%; /* 80px */

    @media (max-width: 965px) {
      font-size: 45px;
    }

    @media (max-width: 840px) {
      font-size: 40px;
    }

    @media (max-width: 743px) {
      font-size: 35px;
    }

    @include mobile {
      margin: 0 auto;
      padding-bottom: 40px;
      max-width: 300px;
      align-items: center;
      text-align: center;
      font-size: 30px;
      line-height: 140%;
    }
  }

  &__wrapper {
    margin-right: 10px;
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__content {
    margin-bottom: 75px;
  }

  &__item {
    @media (max-width: 480px) {
      text-align: center;
    }
  }

  .swiper-slide {
    width: 360px;

    @include small-tablet {
      width: 220px;
    }
  }

  .swiper-scrollbar-drag {
    background-color: #141024;
  }

  &__image {
    border-radius: 20px;
    margin-bottom: 20px;
    max-height: 500px;
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;

    color: #141024;
    font-size: 24px;
    font-weight: 600;
    line-height: 160%; /* 38.4px */

    @include mobile {
      font-weight: 600;
      font-size: 20px;
      line-height: 160%;
    }
  }

  &__post {
    margin: 0;
    margin-bottom: 20px;

    color: rgba(20, 16, 36, 0.7);
    font-size: 18px;
    font-weight: 400;
    line-height: 160%; /* 28.8px */

    @include mobile {
      font-weight: 400;
      font-size: 14px;
      line-height: 160%;
      color: rgba(20, 16, 36, 0.7);
    }
  }

  &__btn {
    color: #d52027;
    font-size: 18px;
    font-weight: 400;
    line-height: 160%; /* 28.8px */

    @include mobile {
      font-size: 16px;
      line-height: 160%;
    }
  }

  &__nav {
    @include flex-all-sb;

    @include mobile {
      justify-content: center;
    }
  }

  &__scroll {
    border-radius: 40px;
    width: 100%;
    max-width: 360px;
    height: 5px;
    background: rgba(20, 16, 36, 0.08);
  }

  &__buttons {
    @include flex-v-center;

    @include mobile {
      display: none;
    }
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;
  }

  &__slider-btn--prev {
    background-image: url("../img/prev.svg");
  }

  &__slider-btn--next {
    background-image: url("../img/next.svg");
  }
}

