.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 30px 0;

  &__container {
    @include flex-all-sb;

    @include high-tablet {
      justify-content: initial;
    }
  }

  &__logo {
    @include mobile {
      max-width: 140px;
    }
  }

  &__navbar {
    @include high-tablet {
      position: fixed;
      left: 0;
      top: 0;
      overflow-y: auto;
      z-index: 100;
      width: 100%;
      height: 100%;
      background-color: #141024;
      opacity: 0;
      visibility: hidden;
      transition: opaccity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
  }

  &__phone {
    color: #fff;
    font-family: Jost;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 48px */

    @include high-tablet {
      position: relative;
      margin-right: 10px;
      margin-left: auto;
      border: 1px solid rgba(255, 255, 255, 0.08);
      border-radius: 10px;
      width: 44px;
      height: 44px;
      font-size: 0;
      background-image: url("../img/phone.svg");
      background-position: center;
      background-size: 24px 24px;
      background-repeat: no-repeat;
    }
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}

.navbar {
  &__list {
    display: flex;
    gap: 40px;

    @include high-tablet {
      @include flex-all-center;

      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__item {
  }

  &__link {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 160%; /* 32px */

    @include high-tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      text-align: center;
      color: #fff;
    }
  }
}
