.graph-modal__close {
  right: 40px;
  top: 40px;
  width: 20px;
  height: 20px;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  background: none;

  svg {
    width: 20px;
    height: 20px;

    @include mobile {
      width: 13px;
      height: 13px;
    }
  }

  @include mobile {
    right: 20px;
    top: 20px;
    width: 13px;
    height: 13px;
  }
}

.graph-modal {
  &::before {
    @include tablet {
      display: none;
    }
  }
}

.popup {
  padding: 40px;
  width: 100%;
  max-width: 940px;

  &__top {
    display: flex;
    margin-bottom: 40px;
  }

  &__image {
    margin-right: 40px;

    @include mobile {
      margin-right: 10px;
      max-width: 120px;
    }
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: #141024;

    @include mobile {
      font-weight: 600;
      font-size: 20px;
      line-height: 160%;
      color: #141024;
    }

    @media (max-width: 390px) {
      font-size: 16px;
    }
  }

  &__post {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: rgba(20, 16, 36, 0.7);

    @include mobile {
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      color: rgba(20, 16, 36, 0.7);
    }

    @media (max-width: 390px) {
      font-size: 12px;
    }
  }

  &__bottom-content {
    padding-top: 20px;
    padding-right: 20px;
    max-height: 375px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: #141024;

    @include mobile {
      font-size: 16px;
    }

    @media (max-width: 390px) {
      font-size: 14px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: #141024;

    @include mobile {
      font-size: 20px;
    }

    @media (max-width: 390px) {
      font-size: 16px;
    }
  }

  .simplebar-track.simplebar-vertical {
    border-radius: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 5px;
    background: rgba(20, 16, 36, 0.08);
  }

  .simplebar-scrollbar::before {
    left: 0;
    right: 0;
    border-radius: 40px;
    width: 5px;
    background-color: #141024;
    opacity: 1;
  }

  @include small-tablet {
    padding-top: 80px;
  }

  @include mobile {
    padding: 20px;
    padding-top: 40px;
  }
}

.social {
  display: flex;
  gap: 10px;

  @include mobile {
    gap: 5px;
  }

  &__item {
  }

  &__link {
  }

  &__svg {
  }
}

.tabs {
  &__menu {
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
    padding-bottom: 20px;
    @media (max-width: 580px) {
      display: flex;
      justify-content: center;
      text-align: center;
      padding-bottom: 10px;
    }
    @media (max-width: 360px) {
      justify-content: flex-start;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 80%;

    @media (max-width: 580px) {
      width: 100%;
      gap: 15px;
    }

    @media (max-width: 460px) {
      gap: 7px;
    }

    &-item {
    }

    &-btn {
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;
      color: #141024;

      @media (max-width: 580px) {
        font-size: 17px;
      }

      @media (max-width: 460px) {
        font-size: 15px;
      }

      &:focus {
        color: #d52027;
        background-color: transparent;
      }

      &--active {
        outline: none;
        color: #d52027;
        background-color: transparent;
      }
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }
}
