.tooltip {
  margin-left: 10px;

  &__btn {
    @include flex-all-center;

    border-radius: 100%;
    width: 24px;
    height: 24px;
    background: rgba(20, 16, 36, 0.08);

    svg {
      width: 4px;
      height: 14px;
    }

    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
    }
  }

    @include hover{
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    border-radius: 5px;
    padding: 15px 20px;
    box-shadow: 0 2px 8px 0px rgba(20, 16, 36, 0.1);

    color: #141024;
    text-align: center;
    font-family: Jost;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 25.6px */
    background: #fff;

    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    @include mobile {
      max-width: 150px;
      padding: 10px 15px;
      font-size: 12px;
    }
  }
}

