.order {
  padding: 87px 0;
  background-color: #d52027;
  background-size: cover;
  background-position: bottom center;

  @media (max-width: 995px) {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }

  &__left {
    grid-column: 5 span;
    padding-left: 60px;

    @include high-tablet {
      grid-column: 12 span;
      margin-bottom: 30px;
      padding-left: 0;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;

    font-weight: 600;
    font-size: 36px;
    line-height: 130%;
    color: #fff;

    @include small-tablet {
      line-height: 70px;
    }

    @include mobile {
      margin: 0 auto;
      padding-bottom: 10px;
      max-width: 300px;
      align-items: center;
      text-align: center;
      font-size: 30px;
      line-height: 140%;
    }
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #fff;

    @include mobile {
      margin: 0 auto;
      max-width: 250px;

      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
      color: #fff;
    }
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 7 span;
    padding-right: 60px;

    @media (max-width: 995px) {
      display: flex;
      grid-column: 12 span;
      justify-content: center;

      padding-right: 0;
    }

    @include mobile {
      max-width: 300px;
      width: 100%;
      padding: 0;
    }
  }

  &__form {
    max-width: 500px;
  }
}
