.hero {
  padding-top: 160px;
  min-height: 800px;
  background-color: #141024;

  @media (max-width: 1170px) {
    min-height: 700px;
  }

  @media (max-width: 1070px) {
    min-height: 600px;
  }

  @media (max-width: 720px) {
    padding-bottom: 40px;
  }

  &__container {
    @include flex-v-center;

    @media (max-width: 720px) {
      position: relative;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  &__content {
    max-width: 516px;
  }

  &__title {
    margin: 0;

    color: #fff;
    font-size: 70px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 112px */
    text-transform: uppercase;

    @media (max-width: 965px) {
      font-size: 53px;
    }

    @media (max-width: 840px) {
      font-size: 40px;
    }

    @media (max-width: 743px) {
      font-size: 35px;
    }

    @include mobile {
      color: #fff;
      text-align: center;
      font-size: 30px;
    }
  }

  &__subtitle {
    display: inline-block;
    max-width: 480px;
    margin-bottom: 50px;

    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 160%; /* 32px */
    text-transform: initial;

    @media (max-width: 965px) {
      font-size: 19px;
    }

    @media (max-width: 840px) {
      font-size: 18px;
    }

    @media (max-width: 743px) {
      font-size: 17px;
    }

    @include mobile {
      display: inline-block;
      color: #fff;
      text-align: center;
      font-family: Jost;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 160%; /* 25.6px */
    }
  }

  &__link {
    @media (max-width: 720px) {
      position: absolute;
      max-width: 300px;
      width: 100%;
      z-index: 1;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__image {
    transform: translateX(167px);

    // @include high-tablet {
    //   display: none;
    // }
    @include medium-desktop {
      max-width: 550px;
    }

    @media (max-width: 1170px) {
      max-width: 500px;
      transform: translateX(90px);
    }

    @media (max-width: 1070px) {
      max-width: 400px;
      transform: translateX(75px);
    }

    @media (max-width: 930px) {
      transform: translateX(10px);
    }

    @media (max-width: 720px) {
      transform: translateX(0);
    }
  }
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 25px 54px;
  min-width: 260px;

  color: #fff;
  text-align: center;
  font-family: Jost;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%; /* 28.8px */
  border-radius: 5px;
  background: #d52027;

  @include mobile {
    color: #fff;
    text-align: center;
    font-family: Jost;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 25.6px */

    display: flex;
    padding: 25px 54px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
  }
}
