.result {
  background-color: #f9f9f9;
  &__container {
  }

  &__title {
    margin: 0;
    padding-bottom: 60px;

    color: #141024;
    font-family: Jost;
    font-size: 50px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 80px */

    @media (max-width: 965px) {
      font-size: 45px;
    }

    @media (max-width: 840px) {
      font-size: 40px;
    }

    @media (max-width: 743px) {
      font-size: 35px;
    }

    @include mobile {
      margin: 0 auto;
      padding-bottom: 40px;
      max-width: 300px;
      align-items: center;
      text-align: center;
      font-size: 30px;
      line-height: 140%;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      align-items: center;
      text-align: center;
    }
  }

  &__item {
    grid-column: 4 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__subtitle {
    display: inline-flex;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;

    color: #141024;
    font-family: Jost;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 38.4px */

    @include mobile {
      margin-bottom: 10px;
      font-size: 20px;
    }
  }

  &__descr {
    margin: 0;
    color: rgba(20, 16, 36, 0.7);
    font-size: 18px;
    font-weight: 400;
    line-height: 160%; /* 28.8px */
  }
}
