.about {
  background-color: #fff;
  &__container {
  }

  &__descr {
    margin: 0;
    max-width: 895px;

    color: #141024;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;

    @include mobile {
      font-size: 20px;
    }
  }

  &__descr-span {
    color: #141024;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 48px */

    @include mobile {
      font-size: 20px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    padding-top: 60px;

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      align-items: center;
      text-align: center;
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__digit {
    margin: 0;

    color: #d52027;
    font-family: Jost;
    font-size: 70px;
    font-style: normal;
    font-weight: 600;
    line-height: 160%; /* 112px */
  }

  &__text {
    margin: 0;

    color: rgba(20, 16, 36, 0.7);
    font-family: Jost;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 28.8px */
  }
}
